import React, { useEffect, useRef, useState } from "react";
import Recording from "./Recording"
import { Result, Form, Progress, Divider, Input, Collapse, Space, Typography, Button, Descriptions} from 'antd';
import YoutubeEmbed from "./YoutubeEmbed";
import Icon, { LeftOutlined, RightOutlined, SmileOutlined } from '@ant-design/icons';
import Score from "./Score";
import SampleComment from "./Comment";
import piece from "./data/pieces.json";
import { useLocation } from "react-router-dom";
import recordings from "./data/recordings.json";
import scores from "./data/scores.json";
import piecef from "./data/piecesf.json";
import steps from "./data/steps.json";
import Confetti from 'react-confetti'

import { render } from "@testing-library/react";
import Item from "antd/lib/list/Item";


const onRecClick = () => {
    document.getElementByIdname("youtube").innerHTML = '<YoutubeEmbed />'
}

function Overview(props) {
    props.setCurr("over");
    const ref = useRef(null);

    if (!props.lang) {
        piece = piecef;
    }

    const [embid, setEmbid] = useState("");
    const setE = (val) => {
        setEmbid(val);
    }

    const location = useLocation();
    // const {id} = loc.state;
    // const id = props.location.sProps.id
    const id = location.state.id.id;

    const onChange = (key) => {
        console.log(key);
      };

    var name, composer, h, r, a, rating, era, inst, tempo, bpm, type, desc, ana;
    // var pieces = require("./data/pieces.json");
    piece.map((data, key) => {
        if (data.id === id) {
            name = data.name;
            composer = data.composer;
            era = data.era;
            inst = data.instrument;
            h = data.henle;
            r = data.rcm;
            a = data.abrsm;
            tempo = data.tempo;
            bpm = data.bpm;
            type = data.type;
            desc = data.desc;
            ana = data.ana;
            rating = data.rating.reduce((a,b)=> a+b,0)/data.rating.length;
        }
    })

    var instructions, instru, n;


    if (steps.filter((data, key) => data.piece === id).length === 0) {
        instructions = false;
    } else {
        instructions = true;
        instru = steps.filter((data, key) => data.piece === id)[0].steps;
        n = steps.filter((data, key) => data.piece === id)[0].n;
    }

    var rec = []

    recordings.map((data, key) => {
        if(data.piece === id) {
            rec.push(
                <Recording name={data.name} date={data.year} rating={data.ratings.reduce((a,b)=> a+b,0)/data.ratings.length} link={data.link} />
            )
        }
    })

    const [learningP, setLearn] = useState(0);

    const inc = () => {
        setProgress(true);
    }

    const [currStep, setStep] = useState(0);

    const nextStep = () => {
        if (currStep === instru.length) {
            ;
        } else if (currStep+1 === instru.length) {
            setLearn(Math.floor(learningP+(100/(n-1))));
            setComplete(true);
        }else if (instru[currStep].includes("Special note #")) {
            setStep(currStep+1);
        } else {
            setLearn(Math.floor(learningP+(100/(n-1))));
            setStep(currStep+1);
        }
    }

    const prevStep = () => {
        if (currStep === 0) {
            ;
        }else if (instru[currStep-1].includes("Special note #")) {
            setStep(currStep-1);
        } else {
            setLearn(Math.floor(learningP-(100/(n-1))));
            setStep(currStep-1);
        }
    }
    

    const [progress, setProgress] = useState(false);
    const [complete, setComplete] = useState(false);


    const {Panel} = Collapse;
    const { TextArea } = Input;
    return (
        <div>
            
            <Space direction="vertical" align="center" style={{ marginTop: '70px', width: '100%', zIndex: '1000' }}>
                <Typography.Title level={1}>{name}</Typography.Title>
                <Typography.Paragraph style={{ fontSize: "20px" }}></Typography.Paragraph>
            {/* <Space direction="horzontal" style={{marginLeft:'100px'}}> */}

                <Descriptions style={{width:'700px', marginRight:'50px'}}>
                    <Descriptions.Item label={props.lang ? "Composer" : "Compositeur"} span={2}>{composer}</Descriptions.Item>
                    <Descriptions.Item label={props.lang ? "MMusical Era" : "Epoque musicale"}  span={2}>{era}</Descriptions.Item>
                    <Descriptions.Item label={props.lang ? "Music type" : "Type de musique"}  span={2}>{type}</Descriptions.Item>
                    <Descriptions.Item label="Instrumentation" span={2}>{inst}</Descriptions.Item>
                    <Descriptions.Item label="Tempo" span={2}>{tempo} ({bpm}bpm)</Descriptions.Item>  
                </Descriptions>
                <Descriptions title={props.lang ? "Difficulty" : "Diffuculté"}  bordered style={{width:'300px', marginBottom:'100px', marginLeft:'0px'}}>
                    <Descriptions.Item label="RCM" span={3}>{r}</Descriptions.Item>
                    <Descriptions.Item label="Henle" span={3}>{h}</Descriptions.Item>
                    <Descriptions.Item label="ABRSM" span={3}>{a}</Descriptions.Item>
                </Descriptions>
            {/* </Space> */}

            </Space>

            <Collapse defaultActiveKey={['1']} onChange={onChange} style={{marginLeft:'100px', marginBottom:'100px', marginRight:'100px'}}>
                <Panel header={props.lang ? "Learn" : "Apprendre"} key="1" >
                <Progress style={{ width:'85%', marginLeft:'7.5%', marginTop:'30px'}}
                    strokeColor={{
                        '0%': '#0e1536',
                        '100%': '#076c75',
                    }}
                    percent={learningP}
                />
            <Space direction="vertical"  align="center" style={{ width:'85%', marginLeft:'7.5%', marginTop:'30px'}}>
                
                {(() => {
                    if (!progress && !complete) {
                        return(<Button type="primary" ghost onClick={inc}>Start Learning</Button>);
                    } else if (progress && !complete) {
                        return (<Space align="center" textAlign="center" style={{textAlign:'center'}}><Button onClick={prevStep} style={{marginLeft:"0px"}}><LeftOutlined /></Button><Divider/><Typography.Paragraph>{instru[currStep]}</Typography.Paragraph><Divider/><Button onClick={nextStep} style={{marginRight:"0px"}}><RightOutlined /></Button></Space>)
                    } else {
                        return (<><Result icon={<SmileOutlined/>} title="Congratulations you finished learning this piece" /><Confetti/></>)
                    }
                    })()
                    }
            </Space>

                </Panel>
                <Panel header={props.lang ? "Description" : "Déscription"} key="2">
                    <p>{desc}</p>
                </Panel>
                <Panel header={props.lang ? "Technical analysis" : "Analyse technique"} key="3">
                    <p>{ana}</p>
                </Panel>
                <Panel header={props.lang ? "Recording" : "Enregistrements"} key="4">
                    <Space direction="vertical" size="middle" style={{ marginBotton: '50px', display: 'flex' }}>
                        {recordings.filter((data, key) => data.piece === id).map((data, key) => {
                            return(<Recording setLink={setE} name={data.name} date={data.year} rating={data.ratings.reduce((a,b)=> a+b,0)/data.ratings.length} link={data.link} />)
                        })}
                        {(() => {
                        if (recordings.filter((data, key) => data.piece === id).length === 0) {
                            return(<Typography.Paragraph style={{textAlign:'center'}}>No recordings to display {":("}</Typography.Paragraph>)
                        }
                    })()
                    }
                        
                    </Space>

                    <YoutubeEmbed embedId={embid}/>
                </Panel>
                <Panel header={props.lang ? "Scores" : "Partitions"} key="5">
                <Space direction="vertical" size="middle" style={{ marginBotton: '50px', display: 'flex' }}>
                    {
                    scores.filter((data, key) => data.piece === id).map((data, key) => {
                        return( <Score publisher={data.publisher} editor={data.editor} date={data.year} urtext={data.urtext} rating={data.ratings.reduce((a,b)=> a+b,0)/data.ratings.length} link={data.pdf} />)
                    })}
                    {(() => {
                        if (scores.filter((data, key) => data.piece === id).length === 0) {
                            return(<Typography.Paragraph style={{textAlign:'center'}}>No scores to display {":("}</Typography.Paragraph>)
                        }
                    })()
                    }
                    
                    
                </Space>
                </Panel>
            </Collapse>

            <Space direction="vertical" style={{ marginLeft:'15%', marginRight:'15%', marginBottom: '100px', width: '70%', zIndex: '1000' }}>

                <SampleComment> 
                    <SampleComment />
                </SampleComment>
                <SampleComment />

                <Form.Item style={{display: 'flex',}}>
                    <TextArea value="Leave comment..." />
                </Form.Item>
                <Form.Item>
                    <Button type="primary">
                        Add Comment
                    </Button>
                </Form.Item>
            </Space>
        </div>
    );
}

export default Overview

